<template>
  <div>
    <content-placeholders class="mt-3" v-if="false">
      <content-placeholders-text :lines="1" />
    </content-placeholders>
    <v-layout v-else row>
      <v-flex xs12 sm6 offset-sm3 mt-5>
        <v-card>
          <nav class="toolbar theme--dark blue darken-2">
            <div class="toolbar__content" style="height: 64px;">
              <div>
                <span>CNG THANH TON DVC TRC TUYN TU THUYN VO, RI CNG BIN </span>
                <br>
                <strong style="text-transform: uppercase;">Cc Hng Hi Vit Nam - B Giao Thng Vn Ti</strong>
              </div>
              <div class="spacer"></div>
            </div>
          </nav>
          <div class="list list--two-line">
            <v-layout v-if="!payment.message" wrap>
              <v-flex xs12 class="text-center">
                Phiu thanh ton khng hp l
              </v-flex>
            </v-layout>
            <v-layout v-else wrap>
              <v-flex xs12 sm6>
                <v-layout wrap>
                  <v-flex xs12 sm7>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>Tn h s: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>{{payment.documentName}}</v-subheader>
                  </v-flex>

                  <v-flex xs12 sm7>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>Nm ng k h s: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>{{payment.documentYear}}</v-subheader>
                  </v-flex>

                  <v-flex xs12 sm7>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>M s thu doanh nghip: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>{{payment.userCreated}}</v-subheader>
                  </v-flex>

                  <v-flex xs12 sm7>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>C quan yu cu np ph: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>{{payment.maritimecode}}</v-subheader>
                  </v-flex>

                  <v-flex xs12 sm7>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>Ngy thng bo np ph: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>{{payment.reportDate | moment("dddd/MMMM/YYYY")}}</v-subheader>
                  </v-flex>

                  <v-flex xs12 sm7>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>Ni dung yu cu np ph: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>{{payment.content}}</v-subheader>
                  </v-flex>

                  <v-flex xs12 sm7>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>S chng t: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>{{payment.debitnotenumber}}</v-subheader>
                  </v-flex>

                  <v-flex xs12 sm7>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader v-else>Tng tin: </v-subheader>
                  </v-flex>
                  <v-flex xs12 sm5>
                    <content-placeholders class="mt-1" v-if="false">
                      <content-placeholders-text :lines="1" />
                    </content-placeholders>
                    <v-subheader class="red--text text--darken-3" v-else>{{payment.fee}}</v-subheader>
                  </v-flex>

                </v-layout>
              </v-flex>
              <v-flex xs12 sm6 class="pr-2">
                <file-pond v-if="showFileUpload"
                  name="test"
                  ref="pond"
                  label-idle="<strong>NH KM TP CHNG T</strong> </br> Ko th hoc ti ln... </br> Chng t thanh ton cho chuyn khon l giy yu cu np tin vo ngn hng hoc ho n chng nhn giao dch chuyn khon c sinh ra"
                  allow-multiple="true"
                  accepted-file-types="image/jpeg, image/png"
                  :server="support.uploadURL + '&_paygateaction_WAR_TichHopGiaoThongportlet_idDebit=' + id"
                  v-bind:files="myFiles"
                  v-on:init="handleFilePondInit"/>
              </v-flex>
            </v-layout>
            <v-layout wrap>
              <v-flex class="text-center mt-4">
                <v-btn :to="'/payment/' + id + '/' + documentname + '/' + documentyear + '/' + maritimecode">
                  <v-icon size="16">undo</v-icon> &nbsp;
                  Quay li
                </v-btn>
                <v-btn color="primary" v-on:click.native="sendBaoNop"
                  :loading="loadingBtn"
                  :disabled="loadingBtn"
                >
                  <v-icon size="16">send</v-icon> &nbsp;
                  Gi bo np chuyn khon 
                </v-btn>
              </v-flex>
            </v-layout>
          </div>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import axios from 'axios'
import support from '../store/support.json'
// Import Vue FilePond
import vueFilePond from 'vue-filepond'

// Import FilePond styles
import 'filepond/dist/filepond.min.css'

// Import image preview plugin styles
import 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css'

// Import image preview and file type validation plugins
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type'
import FilePondPluginImagePreview from 'filepond-plugin-image-preview'

// Create component
const FilePond = vueFilePond(FilePondPluginFileValidateType, FilePondPluginImagePreview)

export default {
  props: ['id', 'documentname', 'documentyear', 'maritimecode'],
  data: () => ({
    loadingBtn: false,
    myFiles: [],
    showFileUpload: true,
    support: support,
    payment: {}
  }),
  components: {
    FilePond
  },
  created () {
    var vm = this
    vm.$nextTick(function () {
      let param = {
        id: vm.id,
        documentname: vm.documentname,
        documentyear: vm.documentyear,
        maritimecode: vm.maritimecode
      }
      vm.$store.dispatch('loadInitResource', param).then(function (result) {
        vm.payment = result
      })
    })
  },
  methods: {
    handleFilePondInit: function () {
      console.log('FilePond has initialized')
    },
    sendBaoNop () {
      let vm = this
      vm.loadingBtn = true
      let formData = new FormData()
      formData.append('fileName', '123')
      axios.post(support.baoNopURL, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(function (reponse) {
        console.log('upload file success!')
        vm.loadingBtn = false
      }).catch(function (xhr) {
        console.log(xhr)
        vm.loadingBtn = false
      })
    }
  }
}
</script>



